:root {
  --black: #000000;
  --blue: #083c6e;
  --lightblue: #e0edff;
  --silver: #c4c4c4;
  --lightgrey: #e9e9e9;
  --text: #b1b6ca;
}

html {
  box-sizing: border-box;
  font-size: 16px;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

html,
body {
  height: 100%;
  position: relative;
}

body {
  font-family: "Poppins";
  font-size: 18px;
  font-weight: 400;
  color: var(--text);
  overflow-x: hidden;
}

ul,
ol,
li,
p,
h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
}

ul li {
  list-style: none;
}

button:focus,
input:focus {
  outline: transparent;
}

a {
  text-decoration: none;
  color: inherit;
}

button {
  padding: 0;
  margin: 0;
  border: 0;
  background-color: transparent;
  border-radius: 0;
  cursor: pointer;
  -webkit-appearance: none;
}

.flex {
  display: flex;
  align-items: flex-start;
  justify-content: flex-start;
}

.--just-space {
  justify-content: space-between;
}

.--just-center {
  justify-content: center;
}

.--just-end {
  justify-content: flex-end;
}

.--align-str {
  align-items: stretch;
}

.--align-center {
  align-items: center;
}

.--align-end {
  align-items: flex-end;
}

.container {
  max-width: 1140px;
  margin: 0 auto;
  padding: 0 15px;
}

.title {
  position: relative;
  padding-bottom: 18px;
  font-weight: 500;
  font-size: 52px;
  color: var(--black);
}

.title::after {
  content: "";
  position: absolute;
  width: 154px;
  height: 6px;
  background-color: var(--blue);
  left: 0;
  bottom: 0;
}

.title--small {
  font-size: 38px;
  line-height: 48px;
}

.subtitle {
  margin-bottom: 4px;
  font-size: 18px;
  color: var(--black);
  letter-spacing: 0.1em;
  text-transform: uppercase;
}

.burger {
  display: none;
  position: relative;
  width: 40px;
  height: 20px;
  background-color: transparent;
  border: none;
  cursor: pointer;
}

.burger::before,
.burger::after {
  content: "";
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 2px;
  background-color: var(--black);
}

.burger::after {
  top: auto;
  bottom: 0;
}

.burger span {
  position: absolute;
  left: 0;
  top: 50%;
  transform: translateY(-50%);
  width: 100%;
  height: 2px;
  display: inline-block;
  background-color: var(--black);
}

.benefits {
  padding-top: 167px;
  padding-bottom: 73px;
}

.benefits__subtitle {
  text-align: left;
}

.benefits__title {
  max-width: 450px;
  margin: 0px 0px 60px 0px;
  text-align: left;
}

.benefits__title::after {
  content: "";
  position: absolute;
  width: 183px;
  height: 7px;
  background-color: var(--blue);
  left: 0;
  bottom: 0;
}

.benifits__body {
  position: relative;
  max-width: 1190px;
  margin: 0 auto;
  left: 26px;
}

.benifits__row {
  display: flex;
  margin: 0 -15.5px;
}

.benifits__column {
  padding: 0 15.5px;
  display: flex;
  flex: 0 1 33.333%;
}

.benifits__item {
  display: flex;
  align-items: flex-start;
}

.benifits__item-img {
  margin-right: 18px;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 96px;
  height: 96px;
  border-radius: 50%;
  box-shadow: 1px 1px 20px rgba(0,0,0,0.07);
}

.benifits__item-img img {
  max-width: 100%;
}

.benifits__item-descr {
  max-width: 265px;
  padding-top: 17px;
}

.benifits__item-title {
  margin: 0px 0px 10px 0px;
  font-weight: 500;
  font-size: 32px;
  color: var(--black);
}

.benifits__item-text {
  font-family: "Rubik";
  font-weight: 400;
  line-height: 28px;
}

.fave {
  padding-top: 155px;
  padding-bottom: 112px;
}

.fave__title {
  margin-bottom: 85px;
  text-align: center;
}

.fave__title--line::after {
  display: none;
}

.fave__items {
  position: relative;
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
}

.fave__items::before {
  background-image: url("../img/fave-bg.svg");
  position: absolute;
  top: -107px;
  left: -54px;
  width: 202px;
  height: 185px;
  content: "";
  z-index: -1;
}

.fave-item {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  width: 357px;
  height: 264px;
  background-position: center center;
  background-repeat: no-repeat;
  background-size: cover;
}

.fave-item__info {
  width: 80px;
  padding: 10px 20px;
  margin-top: 13px;
  margin-left: 13px;
  font-family: "Rubik";
  font-weight: 400;
  font-size: 14px;
  letter-spacing: 0.1em;
  text-transform: uppercase;
  color: #ffffff;
  background-color: var(--blue);
}

.fave-item__footer {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0 12px;
  height: 55px;
  background-color: rgba(8,61,110,0.9);
}

.fave-item__title {
  font-weight: 500;
  font-size: 20px;
  color: #fff;
}

.fave-item__price {
  width: 96px;
  padding: 9.5px 5px;
  font-weight: 500;
  font-size: 18px;
  color: #fff;
  text-align: center;
  border: 1.5px solid #ffffff;
  border-radius: 5px;
}

.furniture {
  padding-top: 37px;
  padding-bottom: 248px;
}

.furniture__inner {
  display: flex;
  justify-content: space-between;
}

.furniture__content {
  padding-top: 35px;
  max-width: 450px;
  flex: 1 1 40%;
}

.furniture__title {
  position: relative;
  margin-bottom: 18px;
}

.furniture__descr {
  margin-bottom: 58px;
  font-size: 18px;
  line-height: 28px;
  color: var(--text);
}

.btn {
  padding: 16px 50px;
  font-family: "DM Sans";
  font-weight: 500;
  font-size: 18px;
  color: #fff;
  letter-spacing: 0.1em;
  text-transform: uppercase;
  background-color: var(--blue);
}

.furniture__image {
  flex: 0 1 43%;
}

.furniture__image-block {
  position: relative;
}

.furniture__image-block::before {
  background-image: url("../img/fave-bg.svg");
  position: absolute;
  right: -65px;
  bottom: -105px;
  width: 202px;
  height: 185px;
  content: "";
  z-index: -1;
}

.furniture__image-bottom {
  position: absolute;
  left: -1px;
  bottom: -24px;
}

.header {
  min-height: 844px;
  background-image: url("../img/bg-header.jpg");
  background-position: top center;
  background-size: cover;
  background-repeat: no-repeat;
}

.header__inner {
  padding-top: 53px;
  margin-bottom: 212px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.header__nav {
  display: flex;
  align-items: center;
}

.nav__list {
  display: flex;
  margin-right: 60px;
}

.nav__item:not(:last-child) {
  margin-right: 60px;
}

.nav__link {
  position: relative;
  padding-bottom: 14px;
  font-weight: 600;
  font-size: 14px;
  letter-spacing: 0.05em;
  color: var(--black);
  text-transform: uppercase;
  transition: color 0.5s;
}

.nav__link::before {
  content: "";
  position: absolute;
  width: 61px;
  height: 3px;
  left: 50%;
  transform: translateX(-50%);
  bottom: 0;
  background-color: var(--blue);
  opacity: 0;
  transition: opacity 0.5s;
}

.nav__link:hover {
  color: var(--blue);
}

.nav__link:hover::before {
  opacity: 1;
}

.header__contact {
  display: inline-block;
  padding: 17px 47px;
  font-family: "DM Sans";
  font-weight: 500;
  font-size: 18px;
  color: #fff;
  letter-spacing: 0.08em;
  text-transform: uppercase;
  background-color: var(--blue);
  border: 2px solid transparent;
  transition: all 0.5s;
}

.header__contact:hover {
  color: var(--blue);
  background-color: #fff;
  border: 2px solid var(--blue);
}

.header__content {
  max-width: 460px;
}

.header__title {
  font-weight: 600;
  font-size: 60px;
  line-height: 86px;
  color: var(--black);
  letter-spacing: 0.1em;
  text-transform: uppercase;
}

.header__descr {
  margin-bottom: 23px;
  font-family: "Rubik";
  font-size: 18px;
  line-height: 28px;
}

.header-form {
  position: relative;
  max-width: 436px;
  width: 100%;
}

.header-form__input {
  padding-left: 37px;
  padding-right: 140px;
  width: 100%;
  height: 50px;
  font-family: "Rubik";
  background-color: #f1f1f1;
}

.header-form__input::placeholder {
  font-family: "Rubik";
  font-size: 16px;
}

.header-form__btn {
  height: 50px;
  padding: 0 37px;
  position: absolute;
  font-weight: 600;
  font-size: 14px;
  color: #fff;
  letter-spacing: 0.05em;
  text-transform: uppercase;
  right: 0;
  background-color: var(--blue);
  border: 2px solid transparent;
  transition: all 0.5s;
  cursor: pointer;
}

.header-form__btn:hover {
  color: var(--blue);
  background-color: #fff;
  border: 2px solid var(--blue);
}

.video-block {
  position: relative;
}

.video-block::before,
.video-block::after {
  position: absolute;
  content: "d";
  font-family: "Satisfy";
  font-size: 400px;
  text-transform: uppercase;
  color: #E0EDFF;
}

.video-block::before {
  top: -156px;
  left: -67px;
  transform: rotate(-43.94deg);
}

.video-block::after {
  bottom: -186px;
  right: -78px;
  z-index: -1;
  transform: matrix(-0.96, 0.27, 0.27, 0.96, 0, 0);
}

.video-block__content {
  position: relative;
  min-height: 446px;
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
}

.video-block__content::after {
  content: "";
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  background-color: rgba(21,34,66,0.5);
}

.play-btn {
  position: absolute;
  width: 124px;
  height: 124px;
  border-radius: 50%;
  border: 1.5px solid #ffffff;
  top: 50%;
  left: 50%;
  transform: translateX(-50%) translateY(-50%);
  z-index: 2;
}

.play-btn__triangle {
  position: absolute;
  width: 0;
  height: 0;
  border-style: solid;
  border-width: 21.5px 0 21.5px 34px;
  border-color: transparent transparent transparent #ffffff;
  top: 50%;
  left: 54%;
  transform: translateX(-50%) translateY(-50%);
}